





















var page = 1,totalpage=1;
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
import mProductList from '@/components/m-product-list'
import mEmpty from "@/components/m-result/m-empty.vue";
export default {
	components: {
		mProductList,
		mEmpty,
		uniLoadMore
	},
	data() {
		return {
			mloading:'more',
			search:"",
			// 附近门店
			nearStoreList:[]
		}
	},
	
	methods:{
		//跳转到商品详情
		goPro(id){
			console.log(id);
			uni.navigateTo({
				url:"/pages/product/product?id="+id
			})
		},
		getProducts(){
			let _this = this;
			uni.showLoading({});
			if(totalpage&&page > totalpage){
				_this.mloading='noMore';
				uni.hideLoading();
				uni.stopPullDownRefresh();
				return ;
			}
			this.mPost("/server/p/search/products",{
				start:page,
				length:20,
				name:_this.search,
			}).then(res=>{
				let data = res.data;
				if(data.list){
						totalpage=data.pages|| 1;
						var newsList = data.list;
						_this.nearStoreList = _this.nearStoreList.concat(newsList);
						page++;	
				}
				uni.hideLoading();
				uni.stopPullDownRefresh();
			}).catch(err=>{
				uni.hideLoading();
				uni.stopPullDownRefresh();
			})
		}
	},
	// 加载更多
	onReachBottom(){
		this.mloading='loading';
		this.getProducts();
	},
	// 重置分页及数据
	onPullDownRefresh(){
		page = 1;
		this.nearStoreList = [];
		this.getProducts();
	},
	onLoad(option){
		this.search=option.search;
		page = 1;
		this.nearStoreList = [];
		this.getProducts();
	}
	
}
